import { writable } from 'svelte/store';
let user = window

function filesController() {
	const { subscribe, set, update } = writable({
        files: [],
    });
	return {
        subscribe,
        add(file)
        {
            update((data)=>{
                data.files.push(file)
                return data;
            });
        },
        restart()
        {
            update((data)=>{
                data.files = [];
                return data;
            });
        },
        remove(file){
            update((data)=>{
                data.files = data.files.filter(( item )=>{
                    if(item.id !== file.id){
                        return item.id;
                    }
                })
                return data;
            });
        }
    }
}


export const FilesController = filesController();