import { server_connection } from '../../../api/PHPserver.js'
import { ChannelData } from '../ChannelData.store';
import axios from 'axios';
import { VideoController } from './VideoController.js';
import { InitVideoUpload } from './InitVideoUpload.js';

class ThumbnailController
{
    constructor(key, paginator){
        this.video_uid = "";
        this.key = key || "";
        this.thumbnails_group = [];
        this.counter = 0;
        this.paginator = paginator;
    }
    parseThumbnails(video_uid, data){
        this.video_uid = video_uid;
        let form = new FormData();
        let counter = 1;

        
        data.map((blob, key)=>{
            console.log(blob);
            form.append('thumbnail-'+key, blob, 'thumbnail-'+key+'.jpg');
            if (counter === 3) {
                this.uploadDefaultThumbnails(video_uid, form);
            }
            counter += 1;
        })
    }
    /**
     * upload default thumbnails.
     */
    uploadDefaultThumbnails(video_uid, form)
    {
        console.log(form);
        let url = `https://stream.3pod.io/api/v1/services/images/thumbnails/${video_uid}`;
        // enviamos mediante axios
        axios.post(url, form, {
            headers: {
                "X-Token": ChannelData.get().channel_token,
                'Content-Type': 'multipart/form-data'
            }
        }).then((data)=>{
        console.log(data);
            ChannelData.get().proccesing_files.map((item, key)=>{
                if (item.stackId === this.key) {
                    item.update_thumbnails = true;
                    console.log("uploar thumbnails --------------------------------------------");
                    if (this.thumbnails_group.length >= 3) {
                        item.thumbnails_group = this.thumbnails_group;
                    }
                    ChannelData.updateProcess(item, "is_loading", 30, this.key);
                }
            })
            ChannelData.get().proccesing_files.map((item, key)=>{
                if (item.uid === video_uid) {
                    let video_controller = new VideoController();
                    video_controller.getSingleVideo(this.video_uid, (response)=>{
                        console.log(response);
                        if (response.internal_status === "ready") {
                            let result = response;
                            result.stackId = item.stackId;
                            result.percentage = item.percentage;
                            result.is_thumbnail_ready = true;
                            console.log(result);
                            ChannelData.updateProcess(result, "is_loading", 30, this.key);
                            ChannelData.deleteStackItem(item.stackId);
                            if (ChannelData.get().stack.length <= 0) {
                                this.paginator.get().submit();
                            }
                        }
                        
                    });
                }
            })
        }).catch((error) => {
            this.counter = this.counter + 1;
            if(this.counter <= 4){
                this.uploadDefaultThumbnails(video_uid, form);
            }
            else{
                this.counter = 0;
                let details = {
                    msg: "Hubo un problema al procesar los thumbnails",
                    type: "uploadDefaultThumbnails",
                    reload: () =>{
                        this.uploadDefaultThumbnails(video_uid, form);
                    }
                }
                ChannelData.errorController(video_uid, details);
            }
        })
    }
    uploadCustomThumbnail(video_uid, file, callback){
        let url = `https://stream.3pod.io/api/v1/services/images/custom_thumbnail/${video_uid}`;
        let form = new FormData();
        const img = new File([file], "thumbnail", { type: "image/jpg" })
        form.append('file', img);
        axios.post(url, form, {
            headers: {
                "X-Token": ChannelData.get().channel_token
            }
        }).then((data)=>{
            callback(data);
        }).catch((error)=>{
            //validar formato de imagen
            console.log(error);
        })
    }
    

}
export { ThumbnailController };